import './App.css';
import List from './mycomponent/List'
import Search from './mycomponent/Search'
import useSemiPersistenceState from './Hooks/useSemiPersistenceState';
import {useEffect,useReducer} from 'react'

const API_ENDPOINT="https://hn.algolia.com/api/v1/search?query=";


const itenReducer=(state,action)=>{
 switch(action.type){
   case"STORIES_FETCH_INIT":
   return{...state,isLoading:true,isError:false};
   case"STORIES_FETCH_SUCESS":
   return{...state,data:action.payload,isLoading:false,isError:false};
   case"STORIES_FETCH_FAILURE":
   return{...state,data:[],isLoading:false,isError:true};
   case"STORIES_REMOVE":
   return{...state,data:state.data.filter((i)=>action.payload.objectID!== i.objectID),}
   default:
     throw new Error();
 }
};


function App() {
  const[searchTerm,setSearchTerm] = useSemiPersistenceState("customSearchTerm","");
  const[iten,dispatchIten]=useReducer(itenReducer,{
    data:[],
    isLoading:false,
    isError:false,
    });
  
  useEffect(() => {
    dispatchIten({type:"STORIES_FETCH_INIT"})
    fetch(`${API_ENDPOINT}react`).then((res)=>res.json()).then((result) =>{dispatchIten({type:"STORIES_FETCH_SUCESS",payload:result.hits});})
    .catch(()=>{dispatchIten({type:"STORIES_FETCH_FAILURE"});} )
  }, [])
  const handleOnSearch = (e) =>{
    setSearchTerm(e.target.value);  
  };
  const onHandleDelete=(name)=>{
    dispatchIten({type:"STORIES_REMOVE",payload:name});
  }
  const filteredItem = iten.data.filter((ite) => ite.title.toLowerCase().includes(searchTerm.toLowerCase()))
  return (
    <div>

      <Search  searchTerm = {searchTerm} onSearch={handleOnSearch}/>
       <h3>SEARCHING FOR:{searchTerm}</h3>
       {iten.isLoading ?(<p>loading...</p>):(<List item={filteredItem} deleted={onHandleDelete}/>)}
       {iten.isError && <p>something went wrong...</p>}
      
      </div>
  );
}

export default App;
